.details_hotel {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("../images/details_hotel_bg.jpg");
  background-size: cover;
  background-position: center;
  padding-top: 24px;
  padding-bottom: 40px;
  .details_hotel_content {
    width: 243px;
    display: flex;
    flex-direction: column;
    & > .details_hotel_logo,
    & > .details_hotel_title {
      margin-left: auto;
    }
  }
  .details_hotel_title {
    font-family: "MontserratBold";
    font-size: 20px;
    color: $white;
    margin-bottom: 20px;
  }
  .details_hotel_text {
    font-family: "MontserratSemiBold";
    color: $text_description;
  }
  .swiper-pagination {
    .icon {
      fill: #fff;
    }
  }
  .button_wrapper {
    flex-wrap: wrap;
    justify-content: center;
    gap: 16px;
    display: flex;
  }
}
